<template>
  <div class="container">
    <v-card>
      <v-card-title>
        <h3>Templates</h3>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>

      <v-data-table
        :headers="headers"
        :items="tableItems"
        :search="search"
        sort-by="calories"
        class="elevation-1"
      >
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editTemplate(item.id)"
            >mdi-pencil</v-icon
          >
          <v-icon small @click="download(item)">mdi-download</v-icon>
          <v-icon
            v-if="user.isAdmin"
            small
            class="ml-2"
            @click="deleteTemplateById(item.id)"
            >mdi-delete-empty</v-icon
          >
        </template>
      </v-data-table>
    </v-card>
    <v-btn v-if="user.isAdmin" class="mt-5" @click="templateCreationSettings">
      <v-icon>mdi-note-plus</v-icon>new template
    </v-btn>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      search: "",
    };
  },
  computed: {
    ...mapState("user", ["user"]),
    ...mapGetters("editor", ["items", "tableItems", "headers"]),
  },
  methods: {
    ...mapActions("editor", [
      "fetchTemplate",
      "fetchTemplates",
      "downloadTemplate",
      "createNewTemplate",
      "deleteTemplate",
    ]),
    templateCreationSettings() {
      this.createNewTemplate();
    },
    editTemplate(templateId) {
      // note: when fetching a template this action takes care of initiation editorjs
      this.fetchTemplate({ id: templateId, isDownloading: false });
    },
    download(template) {
      this.fetchTemplate({ id: template.id, isDownloading: true }).then(
        (template) => {
          this.downloadTemplate(template);
        }
      );
    },
    deleteTemplateById(templateId) {
      const confirmation = confirm(
        "Are you sure you want to delete this template ?"
      );
      if (confirmation) {
        this.deleteTemplate(templateId);
        return;
      }
      return;
    },
  },
  created() {
    this.fetchTemplates();
  },
};
</script>

<style lang="scss" scoped>
.container {
  background-color: #d6dafa;
  padding: 1%;
  border-radius: 10px;
  border-style: solid;
}
</style>
