<template>
  <div class="container">
    <v-card width="900" class="mt-10 mx-auto">
      <v-card-title>Template creation</v-card-title>
      <v-card-text>
        <v-form @submit.prevent ref="form" v-model="valid">
          <v-subheader class="pa-0"
            >What do you which to name your file ?</v-subheader
          >
          <v-text-field
            placeholder="Enter template name"
            v-model="templateName"
            :rules="templateNameRules"
            required
          >
          </v-text-field>
          <v-autocomplete
            v-model="targetUsers"
            :rules="targetUsersRules"
            :items="usersEmails"
            class="mt-3"
            outlined
            dense
            chips
            small-chips
            label="Target users"
            multiple
            required
          ></v-autocomplete>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-btn
          :disabled="!valid"
          @click="validate"
          color="primary"
          large
          class="mx-auto"
          >Next</v-btn
        >
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters("user", ["usersEmails"]),
  },
  methods: {
    ...mapActions("user", ["fetchUsers", "setTargetUsers"]),
    ...mapActions("editor", ["initEditor", "setTemplateName"]),
    validate() {
      if (this.$refs.form.validate()) {
        this.setTargetUsers(this.targetUsers);
        this.setTemplateName(this.templateName);
        this.initEditor();
      }
    },
  },
  data() {
    return {
      templateName: "",
      targetUsers: [],
      valid: false,
      templateNameRules: [
        (v) => !!v || "Template name is required",
        (v) => v.length > 6 || "Template name must be more than 6 characters",
      ],
      targetUsersRules: [(v) => v.length > 0 || "Target users are required"],
    };
  },

  created() {
    this.fetchUsers();
  },
};
</script>

<style lang="scss" scoped>
.container {
  background-color: #d6dafa;
  padding-top: 1%;
  padding-bottom: 5%;
  border-radius: 10px;
  border-style: solid;
}
</style>
