<template>
    <v-app>
        <v-navigation-drawer
                v-bind:drawer="drawer"
                v-model="drawer"
                :clipped="$vuetify.breakpoint.lgAndUp"
                app
        >
            <v-list dense>
                <template v-for="item in items">
                    <v-row v-if="item.heading" :key="item.heading" align="center">
                        <v-col cols="6">
                            <v-subheader v-if="item.heading">{{ item.heading }}</v-subheader>
                        </v-col>
                        <v-col cols="6" class="text-center">
                            <a href="#!" class="body-2 black--text">EDIT</a>
                        </v-col>
                    </v-row>

                    <v-list-group
                            v-if="item.children"
                            :key="item.text"
                            @click="toggleArrow"
                            :prepend-icon="model ? item.icon : item['icon-alt']"
                            append-icon
                    >
                        <template v-slot:activator>
                            <v-list-item-content>
                                <v-list-item-title>{{ item.text }}</v-list-item-title>
                            </v-list-item-content>
                        </template>
                        <v-list-item
                                v-for="(child, i) in item.children"
                                :key="i"
                                @click="editTemplate(child.id)"
                                link
                        >
                            <v-list-item-action v-if="child.icon">
                                <v-icon>{{ child.icon }}</v-icon>
                            </v-list-item-action>
                            <v-list-item-content>
                                <v-list-item-title>{{ child.text }}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list-group>
                </template>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar
                :clipped-left="$vuetify.breakpoint.lgAndUp"
                app
                color="#191970"
                dark
        >
            <v-app-bar-nav-icon @click.stop="drawer = !drawer"/>
            <v-toolbar-title style="width: 300px;" class="ml-0 pl-4">
        <span class="hidden-sm-and-down">
          <v-btn text rounded @click="goHome">Edit Me</v-btn>
        </span>
            </v-toolbar-title>
            <v-spacer/>
            <ProfileDialog :dialog="dialog" @toggleDialog="toggleDialog"/>
            <v-btn text @click="toggleDialog">
                <v-avatar size="36">
                    <img :src="avatar"/>
                </v-avatar>
            </v-btn>
            <a v-if="isAdmin" href="/login">
                <v-btn text rounded>
                    <v-icon>mdi-shield-outline</v-icon>
                    Voyager
                </v-btn>
            </a>

            <v-btn @click="endSession" text rounded>
                <v-icon>mdi-logout</v-icon>
                Logout
            </v-btn>
        </v-app-bar>
        <v-content>
            <v-container class="container">
                <!-- editor & templates & new template form go here -->
                <component :is="dynamicComponent"/>
            </v-container>
        </v-content>
    </v-app>
</template>

<script>
    import {mapState, mapGetters, mapActions} from "vuex";
    import NewTemplateForm from "@/components/editor-components/NewTemplateForm.vue";
    import Editor from "@/components/editor-components/Editor.vue";
    import ProfileDialog from "@/components/ProfileDialog.vue";
    import Templates from "@/components/editor-components/Templates.vue";

    export default {
        data() {
            return {
                drawer: null,
                model: false,
                dialog: false,
            };
        },
        computed: {
            dynamicComponent() {
                if (!this.isEditorHome && !this.isNewTemplate) {
                    return Editor;
                }
                if (!this.isEditorHome && this.isNewTemplate) {
                    return NewTemplateForm;
                }
                if (this.isEditorHome && !this.isNewTemplate) {
                    return Templates;
                }
                return null;
            },
            ...mapState("editor", {
                isEditorHome: (state) => state.isEditorHome,
                isNewTemplate: (state) => state.isNewTemplate,
            }),
            ...mapState("user", {
                avatar: (state) => state.user.avatar,
                isAdmin: (state) => state.user.isAdmin,
            }),
            ...mapGetters("editor", ["items"]),
        },
        methods: {
            // to-refactor: handling the profile loading should be on the user module
            ...mapActions("editor", ["loadProfile", "fetchTemplate", "destroyEditor"]),
            ...mapActions("user", ["logout"]),
            toggleArrow() {
                this.model = !this.model;
            },
            goHome() {
                //to-refactor : this reload can be avoided
                location.reload();
            },
            endSession() {
                this.logout();
            },
            editTemplate(templateId) {
                this.fetchTemplate({id: templateId, isDownloading: false});
            },
            toggleDialog() {
                this.dialog = !this.dialog;
            },
        },
        components: {
            NewTemplateForm,
            ProfileDialog,
            Editor,
            Templates,
        },
    };
</script>

<style scoped></style>
