<template>
  <v-row justify="center">
    <Loading :dialog="loadingDialog" />
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline mx-auto mt-5">
            <a>
              <v-avatar size="90">
                <img :src="logoImage" :alt="username" />
              </v-avatar>
            </a>
            <p class="mx-auto mt-5">{{ username | capitalize }}</p>
          </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-card>
              <v-card-title>
                <div>Customer informations:</div>
              </v-card-title>
              <v-card-text>
                <p>Phone: {{ phone }}</p>
                <p>Email: {{ email }}</p>
                <v-file-input :rules="rules" label="Logo image" v-model="logo" />
                <v-file-input :rules="rules" label="Avatar image" v-model="avatar" />
                <p>Primary Color:</p>
                <v-color-picker
                  class="ma-2"
                  v-model="primaryColor"
                  hide-inputs
                ></v-color-picker>
                <p>Secondary Color:</p>
                <v-color-picker
                  class="ma-2"
                  hide-inputs
                  v-model="secondaryColor"
                ></v-color-picker>
              </v-card-text>
            </v-card>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggleDialog">Close</v-btn>
          <v-btn color="blue darken-1" text @click="save">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import Loading from "@/components/Loading.vue";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      loadingDialog: false,
      logo: [],
      avatar: [],
      rules: [
        value => !value || value.size < 1000000 || 'Avatar size should be less than 2 MB!',
      ],
      primaryColor: this.userPrimaryColor,
      secondaryColor: this.userSecondaryColor,
    };
  },
  props: {
    dialog: {
      type: Boolean,
      default: false,
      required: true,
    },
  },
  computed: {
    ...mapState("user", {
      username: (state) => state.user.name,
      email: (state) => state.user.email,
      phone: (state) => state.user.phone,
      logoImage: (state) => state.user.logo,
      userPrimaryColor: (state) => state.user.primaryColor,
      userSecondaryColor: (state) => state.user.secondaryColor,
    }),
  },
  methods: {
    ...mapActions("user", ["updateColors"]),
    ...mapActions("editor", ["uploadImage"]),
    toggleDialog() {
      this.$emit("toggleDialog");
    },
    toggleLoadingDialog() {
      this.loadingDialog = !this.loadingDialog;
    },
    save: async function () {
      this.toggleLoadingDialog();
      if (this.logo !== undefined && this.logo.length !== 0) {
        await this.uploadImage({ file: this.logo, uploadType: "Logo" });
      }
      if (this.avatar !== undefined && this.avatar.length !== 0 ) {
        await this.uploadImage({ file: this.avatar, uploadType: "Avatar" });
      }
      if (this.primaryColor.hex || this.secondaryColor.hex) {
        await this.updateColors({
          primary: this.primaryColor.hex,
          secondary: this.secondaryColor.hex,
        });
      }
      this.toggleLoadingDialog();
      this.toggleDialog();
    },
  },

  filters: {
    capitalize: function (value) {
      if (!value) return "";
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
  },
  components: {
    Loading,
  },
};
</script>

<style lang="scss" scoped>
.picker {
  display: inline-block;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(3, 1, 1, 0.08);
}
</style>
