<template>
  <div class="container">
    <div class="editor" id="editor-holder"></div>
    <v-btn
      @click="saveTemplate"
      :disabled="isSaveBtnDisabled"
      large
      rounded
      color="primary"
      class="mt-5 mx-auto"
      id="save-template-btn"
      >Save</v-btn
    >
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      isSaveBtnDisabled: false,
    };
  },
  computed: {
    ...mapState("user", ["targetUsers", "user"]),
  },
  methods: {
    ...mapActions("editor", ["saveEditedTemplate", "saveNewTemplate"]),
    saveTemplate() {
      this.isSaveBtnDisabled = true;
      // note: saving the template takes care of destorying the editorjs instance
      if (this.targetUsers.length !== 0) {
        this.saveNewTemplate(this.targetUsers);
      } else {
        this.saveEditedTemplate();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  padding: 1%;
  border-radius: 10px;
  border-style: solid;
}
</style>
